@charset "UTF-8";
/* ————————————––––––––––––––––––––––––––––
	Main CSS Style Sheet
	Author: Mario Souza
	Date: 2018
————————————–––––––––––––––––––––––––––– */
/* ——————————————————————————————————————————
	=FORM RESET
—————————————————————————————————————————— */
textarea,
select,
input,
button,
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"] {
  box-sizing: border-box;
  background-clip: padding-box;
  border-radius: 0;
  -webkit-appearance: none;
  background-color: #fff;
  color: #000;
  margin: 0;
  padding: 0;
  text-align: left;
  font-size: 1em;
  height: auto;
  vertical-align: middle;
}

select {
  background: #fff url(data:image/png;base64,R0lGODlhDQAEAIAAAAAAAP8A/yH5BAEHAAEALAAAAAANAAQAAAILhA+hG5jMDpxvhgIAOw==);
  background-repeat: no-repeat;
  background-position: 97% center;
  padding: 0 25px 0 8px;
  font-size: .875em;
}

/* ——————————————————————————————————————————
	=Normalize
	v8.0.0 | MIT License
—————————————————————————————————————————— */
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/* ——————————————————————————————————————————
	=WordPress Core
—————————————————————————————————————————— */
.alignnone {
  margin: 5px 0;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  /* Image does not overflow the content area */
  padding: 5px 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.screen-reader-text:focus {
  background-color: #eee;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

/* ——————————————————————————————————————————
	=GLOBAL
—————————————————————————————————————————— */
html {
  font-size: 100%;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4 {
  font-weight: 400;
}

a {
  color: inherit;
  text-decoration: none;
}

a:visited {
  color: inherit;
}

p {
  line-height: 1.64;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

.no-wrap {
  white-space: nowrap;
}

.cf:after {
  content: '';
  clear: both;
  display: table;
}

.hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.contentContainer {
  padding: 1rem;
  margin: 1rem auto;
  max-width: 1200px;
}

.contentContainer section {
  margin-bottom: 4rem;
}

.contentContainer .section--bottom-space {
  margin-bottom: 4rem;
}

.contentContainer a {
  font-weight: 400;
}

/* ——————————————————————————————————————————
	=HEADER
—————————————————————————————————————————— */
.header {
  transition: all .25s ease-in-out;
}

.header__logo {
  margin: 0;
  z-index: 20;
  font-size: 1.75rem;
  letter-spacing: 0.0625rem;
  font-family: "Inconsolata", monospace;
}

.header__logo a {
  transition: .25s;
}

.header__logo a:hover {
  color: #787878;
}

@media (hover: none) {
  .header__logo a:hover {
    color: inherit;
  }
}

.globalNav__list,
.globalNav__social-list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.globalNav__list li {
  font-weight: 400;
  text-transform: uppercase;
}

.globalNav__list li a {
  position: relative;
}

.globalNav__list li a:after {
  background-color: #000;
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -2px;
  left: 0;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.25s linear;
}

.globalNav__list li a:hover:after {
  visibility: visible;
  transform: scaleX(1);
}

@media (hover: none) {
  .globalNav__list li a:hover:after {
    visibility: hidden;
    transform: scaleX(0);
  }
}

@media screen and (max-width: 767px) {
  .header {
    padding: 1.25rem 1rem;
  }
  .header__mobile-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .navicon {
    display: block;
    z-index: 20;
  }
  .navicon .navicon__line {
    height: 2px;
    background-color: #000;
    display: block;
    transition: all 0.3s ease-in-out;
  }
  .navicon .navicon__line:nth-child(2) {
    margin-top: 8px;
    margin-bottom: 8px;
  }
  .navicon:hover {
    cursor: pointer;
  }
  .navicon.is-active .navicon__line:nth-child(2) {
    opacity: 0;
  }
  .navicon.navicon--angled .navicon__line {
    margin-left: auto;
  }
  .navicon.navicon--angled .navicon__line:nth-child(1) {
    width: 35px;
  }
  .navicon.navicon--angled .navicon__line:nth-child(2) {
    width: 30px;
  }
  .navicon.navicon--angled .navicon__line:nth-child(3) {
    width: 25px;
  }
  .navicon.navicon--angled.is-active .navicon__line {
    width: 35px;
  }
  .navicon.navicon--angled.is-active .navicon__line:nth-child(1) {
    transform: translateY(10px) rotate(-45deg);
  }
  .navicon.navicon--angled.is-active .navicon__line:nth-child(3) {
    transform: translateY(-10px) rotate(45deg);
  }
  .navicon.navicon--standard .navicon__line {
    width: 35px;
  }
  .navicon.navicon--standard.is-active .navicon__line:nth-child(1) {
    transform: translateY(10px) rotate(45deg);
  }
  .navicon.navicon--standard.is-active .navicon__line:nth-child(3) {
    transform: translateY(-10px) rotate(-45deg);
  }
  .globalNav {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 20;
    margin-top: 4.5rem;
    padding: .5rem 1rem 2rem 1rem;
    box-sizing: border-box;
    text-align: right;
  }
  .globalNav__list li {
    font-size: 1.5rem;
    line-height: 2.85;
  }
  .globalNav__social-list {
    margin-top: 1rem;
  }
  .globalNav__social-item {
    display: inline-block;
    margin-left: 1.5rem;
  }
  .globalNav__social-item:first-child {
    margin-left: 0;
  }
  .globalNav__social-item a {
    display: block;
  }
  .globalNav__social-item .svg-inline--fa {
    font-size: 2rem;
  }
  .globalNav__social-link {
    display: block;
  }
  .nav-closed .globalNav {
    visibility: hidden;
    opacity: 0;
    right: -100%;
    transition: all .45s ease-in;
  }
  .nav-open {
    overflow: hidden;
    position: fixed;
    width: 100%;
  }
  .nav-open .globalNav {
    visibility: visible;
    opacity: 1;
    right: 0;
    overflow-y: auto;
    transition: all .85s ease-out;
  }
}

@media screen and (min-width: 768px) {
  .header {
    padding: 1.75rem 1rem;
  }
  .header__global-container {
    justify-content: space-between;
    display: flex;
    align-items: center;
  }
  .navicon {
    display: none;
  }
  .globalNav__social-list {
    display: none;
  }
  .globalNav__list li {
    display: inline-block;
    margin-left: .75rem;
  }
  .globalNav__list li:first-child {
    margin-left: 0;
  }
  .globalNav__list li.current-menu-item a:after {
    visibility: visible;
    transform: scaleX(1);
  }
  .nav-open .modal.is-active {
    display: none;
  }
}

/* ——————————————————————————————————————————
	=FOOTER
—————————————————————————————————————————— */
.footer__social-list {
  list-style: none;
  padding-left: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.footer__social-item {
  display: inline-block;
}

.footer__social-item:first-child {
  margin-left: 0;
}

.footer__social-link {
  display: block;
  color: #000;
  transition: .25s ease-in-out;
}

.footer__social-link:hover {
  color: #989898;
}

@media (hover: none) {
  .footer__social-link:hover {
    color: inherit;
  }
}

.footer__copyright {
  font-size: .875rem;
}

@media screen and (max-width: 767px) {
  .footer {
    padding: 1.25rem 1rem;
  }
  .footer__social {
    margin-bottom: 1.5rem;
  }
  .footer__social,
  .footer__copyright {
    text-align: center;
  }
  .footer__social-item {
    margin-left: 2rem;
  }
  .footer__social-item .svg-inline--fa {
    font-size: 2rem;
  }
}

@media screen and (min-width: 768px) {
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.75rem 1rem;
  }
  .footer__copyright {
    order: 1;
  }
  .footer__social {
    order: 2;
  }
  .footer__social-item {
    margin-left: 1.25rem;
  }
  .footer__social-item .svg-inline--fa {
    font-size: 1.5rem;
  }
}

/* ——————————————————————————————————————————
	=CTAs
—————————————————————————————————————————— */
.borderLink a {
  color: #000;
  text-decoration: none;
  position: relative;
  white-space: nowrap;
  transition: color .25s;
}

.borderLink a:after {
  content: '';
  position: absolute;
  top: 0;
  left: -1px;
  right: -1px;
  bottom: -2px;
  z-index: -1;
  border-bottom-width: 2px;
  border-bottom-style: solid;
  border-bottom-color: #989898;
  transition: all .25s ease-out;
  opacity: .75;
}

.borderLink a:hover:after {
  border-bottom-width: calc(1em * 1.2);
  transition: all .25s ease-in;
  opacity: .5;
}

@media (hover: none) {
  .borderLink a:hover:after {
    border-bottom-width: 3px;
    opacity: .75;
  }
}

.arrowLink {
  display: inline-block;
  font-family: "Inconsolata", monospace;
}

.arrowLink a {
  display: flex;
  align-items: center;
  transition: color .25s;
}

.arrowLink svg {
  font-size: 1.5rem;
  position: relative;
  transition: left .25s ease-in-out;
  left: 0;
}

.arrowLink:hover {
  color: gray;
}

@media (hover: none) {
  .arrowLink:hover {
    color: inherit;
  }
}

.arrowLink--back svg {
  margin-right: 5px;
}

.arrowLink--back:hover svg {
  left: -5px;
}

@media (hover: none) {
  .arrowLink--back:hover svg {
    left: 0;
  }
}

.arrowLink--forward svg {
  margin-left: 5px;
}

.arrowLink--forward:hover svg {
  left: 5px;
}

@media (hover: none) {
  .arrowLink--forward:hover svg {
    left: 0;
  }
}

.button {
  font-family: "Inconsolata", monospace;
  font-size: 1.125rem;
  padding: .5rem .75rem;
  border: 1px solid #000;
  cursor: pointer;
  background-image: linear-gradient(#000, #000);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 0% 100%;
  transition: all .2s ease-in;
}

.button:hover {
  color: #fff;
  background-size: 100% 100%;
  transition: all .2s ease-out;
}

@media (hover: none) {
  .button:hover {
    color: inherit;
    background-size: 0% 100%;
  }
}

/* ——————————————————————————————————————————
	=ICONS
—————————————————————————————————————————— */
.icon__container {
  display: flex;
  align-items: center;
}

.icon__container .fa-linkedin-in + .icon__text {
  margin-top: 2px;
}

.icon__text {
  margin-left: .5rem;
}

/* ——————————————————————————————————————————
	=MODAL
—————————————————————————————————————————— */
.modal {
  position: fixed;
  visibility: hidden;
  height: 0;
  top: -100%;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #989898;
  opacity: .95;
  z-index: 10;
  transition: all .75s ease-in;
}

.modal.is-active {
  visibility: visible;
  height: 100%;
  top: 0;
  transition: all .55s ease-out;
}

/* ——————————————————————————————————————————
	=FORMS
—————————————————————————————————————————— */
.form input,
.form button {
  padding: .5rem .75rem;
  border: 1px solid #000;
  font-family: inherit;
  height: 18px;
  box-sizing: content-box;
}

.form input:first-of-type {
  margin-right: .25rem;
}

.errorMessage {
  color: red;
  font-weight: 400;
  margin: .25rem 0 0 0;
}

/* ——————————————————————————————————————————
	=HOMEPAGE
—————————————————————————————————————————— */
@media screen and (min-width: 768px) {
  .intro__copy-container {
    width: calc(66.66% - 1rem/3);
    max-width: 720px;
  }
}

/* ——————————————————————————————————————————
	=GRID
—————————————————————————————————————————— */
.grid__title {
  font-size: 1.125rem;
  line-height: 1.4;
  font-family: "Inconsolata", monospace;
}

.grid__item {
  position: relative;
  margin-bottom: 1rem;
}

.grid__item:hover .grid__item-img {
  transform: scale(1.1);
}

@media (hover: none) {
  .grid__item:hover .grid__item-img {
    transform: scale(1);
  }
}

.grid__item:hover .grid__item-copy-container {
  background-color: rgba(0, 0, 0, 0.5);
}

.grid__item-link {
  display: block;
  overflow: hidden;
}

.grid__item-img {
  display: block;
  margin: auto;
  transition: all .5s ease-in-out;
  box-shadow: inset 0 0 100px rgba(0, 0, 0, 0.04);
}

.grid__item-copy-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
}

.grid__item-title {
  font-size: 1.5rem;
  text-align: center;
  margin: 0;
  box-sizing: border-box;
}

.grid__item-title:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -2px;
  left: 0;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.25s linear;
}

.grid__item-title span {
  display: block;
  font-weight: 600;
  font-size: .75rem;
  text-transform: uppercase;
}

.grid__item-title.grid__item-title--visible {
  position: relative;
}

.grid__item-title.grid__item-title--hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.grid__item-title.grid__item-title--light {
  color: #fff;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.9);
}

.grid__item-title.grid__item-title--light:after {
  background-color: #fff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.9);
}

.grid__item-title.grid__item-title--dark {
  color: #000;
  text-shadow: 0 0 4px rgba(205, 205, 205, 0.9);
}

.grid__item-title.grid__item-title--dark:after {
  background-color: #000;
  box-shadow: 0 0 2px rgba(205, 205, 205, 0.9);
}

.grid__cta--home {
  text-align: center;
}

.grid__cta--home a {
  display: inline-block;
}

@media screen and (min-width: 576px) {
  .grid__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
  }
}

@media screen and (min-width: 576px) and (max-width: 767px) {
  .grid__item.grid__item--blog {
    width: calc(50% - 1rem/2);
    margin-right: 1rem;
  }
  .grid__item.grid__item--blog:nth-child(even), .grid__item.grid__item--blog:last-of-type {
    margin-right: 0;
  }
  .grid__item.grid__item--home {
    max-width: 520px;
  }
}

@media screen and (min-width: 768px) {
  .grid__item {
    width: calc(33.33% - 2rem/3);
    margin-right: 1rem;
  }
  .grid__item:nth-child(3n+0), .grid__item:last-of-type {
    margin-right: 0;
  }
}

/* ——————————————————————————————————————————
	=ABOUT PAGE
—————————————————————————————————————————— */
.about__row {
  margin-bottom: 3rem;
}

.about__row:last-child {
  margin-bottom: 0;
}

.about__name {
  font-family: "Inconsolata", monospace;
}

.about__link-list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.about__icon-container .svg-inline--fa {
  font-size: 1.5rem;
}

.about__icon-container.about__icon-container--link {
  transition: all .25s ease-in-out;
}

.about__icon-container.about__icon-container--link:hover {
  color: #989898;
}

@media (hover: none) {
  .about__icon-container.about__icon-container--link:hover {
    color: inherit;
  }
}

@media screen and (max-width: 575px) {
  .about__link-list li {
    margin-bottom: 2rem;
    display: flex;
  }
  .about__link-list li:last-child {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 576px) {
  .about__link-list {
    display: flex;
    align-items: center;
  }
  .about__link-list li {
    margin-right: 1.5rem;
    display: inline-block;
  }
  .about__link-list li:last-child {
    margin-right: 0;
  }
}

@media screen and (max-width: 767px) {
  .about__image-container img {
    display: block;
    margin: auto;
  }
}

@media screen and (min-width: 768px) {
  .about_title {
    margin-top: 0;
    margin-bottom: 2rem;
  }
  .about__content-container {
    display: flex;
  }
  .about__details-container {
    width: calc(50% - 1rem);
    margin-right: 2rem;
    display: inline-block;
    order: 1;
  }
  .about__image-container {
    width: calc(50% - 1rem);
    order: 2;
  }
  .about__name {
    margin-top: 0;
  }
}

/* ——————————————————————————————————————————
	=BLOG
—————————————————————————————————————————— */
.hero {
  position: relative;
  overflow: hidden;
}

.hero__image {
  display: block;
}

.hero__title {
  position: absolute;
  margin: 0;
  letter-spacing: 0.125rem;
}

.hero__title.hero__title--visible {
  transform: translateY(-50%);
  top: 50%;
  left: 10%;
}

.hero__title.hero__title--hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.hero__title.hero__title--light {
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.75);
  color: #fff;
}

.hero__title.hero__title--dark {
  text-shadow: 0 0 5px rgba(205, 205, 205, 0.75);
  color: #000;
}

@media screen and (max-width: 575px) {
  .hero__title {
    font-size: 2.25rem;
  }
}

@media screen and (min-width: 576px) and (max-width: 767px) {
  .hero__title {
    font-size: 2.5rem;
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .hero__title {
    font-size: 2.75rem;
  }
}

@media screen and (min-width: 992px) {
  .hero__title {
    font-size: 3rem;
  }
}

/* =Mario Animations */
@media screen and (max-width: 767px) {
  .hero__mario {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .hero__mario {
    position: absolute;
    left: 0;
    width: 32.5%;
    bottom: 11%;
    text-align: right;
  }
}

@media screen and (min-width: 768px) {
  .hero__mario img {
    width: 3vw;
    max-width: 36px;
    height: 4.167vw;
    max-height: 50px;
  }
}

/* ——————————————————————————————————————————
	=POST
—————————————————————————————————————————— */
.post__row {
  margin-bottom: 2.5rem;
}

.post__details,
.post__copy {
  max-width: 700px;
}

.post__details hr {
  border: 1px solid black;
  margin: .5rem 0;
}

.post__details-list {
  list-style: none;
  padding-left: 0;
  margin: 0;
  line-height: 1.64;
}

.post__details-list li {
  white-space: nowrap;
}

.post__details-list li span {
  font-weight: 400;
}

.post__copy h2,
.post__copy h3,
.post__copy h4 {
  margin-bottom: 0;
}

.post__copy h2 + p,
.post__copy h3 + p,
.post__copy h4 + p {
  margin-top: .75rem;
}

.post__copy h2 {
  margin-top: 2rem;
}

.post__copy ul,
.post__copy ol {
  line-height: 1.25;
}

.post__copy ul li,
.post__copy ol li {
  margin-bottom: 0.3125rem;
}

.post__copy blockquote {
  font-size: 1.25rem;
  font-style: italic;
  position: relative;
}

.post__copy blockquote:before {
  content: '\201C';
  font-size: 2.5rem;
  color: #989898;
  position: absolute;
  top: -.375rem;
  left: -1.5rem;
  font-weight: 400;
}

.post__copy blockquote p {
  margin: 0;
}

.post__image img {
  display: block;
}

@media screen and (max-width: 575px) {
  .post__image img {
    margin: auto;
  }
  .post__image--double img {
    margin-bottom: 1rem;
  }
}

@media screen and (min-width: 576px) {
  .post__image--double {
    display: flex;
    justify-content: center;
  }
  .post__image--double .post__image-container {
    max-width: calc(50% - 1rem/2);
  }
  .post__image--double .post__image-container:first-of-type {
    margin-right: 1rem;
  }
}

@media screen and (max-width: 991px) {
  .post__details,
  .post__copy {
    margin: auto;
  }
  .post__details {
    margin-bottom: 2.5rem;
  }
  .post__details hr {
    width: 25%;
  }
}

@media screen and (min-width: 992px) {
  .post__row:first-child {
    display: flex;
    justify-content: space-around;
  }
  .post__details {
    margin-right: 3rem;
  }
  .post__details hr {
    width: calc(100% + 1rem);
  }
  .post__copy {
    margin: auto;
  }
  .post__copy.post__copy--intro {
    margin: inherit;
    flex-basis: 75%;
  }
  .post__copy.post__copy--intro > h2:first-child {
    margin-top: 2px;
  }
  .post__copy p:first-child {
    margin-top: 1.125rem;
  }
}

